(function () {
    'use strict';
    function FsUUID() {
        this._s4 = function () {
            return Math.floor((1 + Math.random()) * 0x10000)
                .toString(16)
                .substring(1);
        };
        this.generateUUID = function () {
            return this._s4() + this._s4() + this._s4() + this._s4();
        };
    }
    angular.module('Fortscale.analytics')
        .service('fsUUID', FsUUID);
}());
